.nav {
  height: 40px;
  width: 40px;
  border-radius: 100%;
  background-color: #e6ff00;
  z-index: 50;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  top: 20px;

  .burgerBtn {
    width: 30px;
    height: 20px;
    position: relative;
    right: 0;
    margin-right: 0px;
    cursor: pointer;

    span {
      position: absolute;
      top: 8px;
      right: 0;
      width: 20px;
      background-color: black;
      height: 3px;
      transform: translateX(-10px);
    }
  }

  .burgerBtn:before {
    content: '';
    position: absolute;
    top: 0;
    right: 10px;
    width: 10px;
    background-color: black;
    height: 3px;
  }

  .burgerBtn:after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 10px;
    width: 10px;
    background-color: black;
    height: 3px;
  }
}


.burgerBtn_active {
  width: 30px;
  height: 20px;
  position: relative;
  right: -5px;
  margin-right: 10px;
  cursor: pointer;
}

.burgerBtn_active:before {
  content: '';
  position: absolute;
  top: 8px;
  width: 30px;
  background-color: black;
  height: 3px;
  transform: rotate(45deg);
  transition: ease 0.4s;
}

.burgerBtn_active:after {
  content: '';
  position: absolute;
  bottom: 9px;
  width: 30px;
  background-color: black;
  height: 3px;
  transform: rotate(-45deg);
  transition: ease 0.4s;
}